/* Add padding to container elements */
.newProjectForm {
  padding: 16px;
}
.newProjectModal {
  /*display: none;*/ /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: #474e5dad;
  padding-top: 50px;
}
.newProjectModal-content {
  background-color: #fefefe;
  margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
  border: 1px solid #888;
  width: 80%; /* Could be more or less, depending on screen size */
}
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 15px;
}
select {
  margin-bottom: 15px;
  margin-left: 15px;
}
.formHeader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}
.closeForm {
  font-size: 40px;
  font-weight: lighter;
  color: #000000;
}
.closeForm:hover,
.closeForm:focus {
  color: #666666;
}
.closeForm:hover,
.closeForm:focus,
.cancelbtn:hover,
.cancelbtn:focus,
.signupbtn:hover,
.cancelbtn:focus {
  cursor: pointer;
}

/* Extra styles for the cancel button */
.cancelbtn {
  background-color: #fa6e64;
}
.signupbtn {
  background-color: #69c1f5;
}

/* Float cancel and signup buttons and add an equal width */
.cancelbtn, .signupbtn {
  color: #ffffff;
  padding: 10px 20px;
  border-radius: 10px;
  width: 25%;
}
.newProjectButtons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

