.btn {
  border: none;
  font-family: 'Lato';
  font-size: inherit;
  color: inherit;
  background: none;
  cursor: pointer;
  padding: 5px 80px;
  display: inline-block;
  margin: 5px 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.btn:after {
  content: '';
  position: absolute;
  z-index: -1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

/* Pseudo elements for icons */
.btn:before {
  font-family: 'FontAwesome';
  /* speak: none; */
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  position: relative;
  -webkit-font-smoothing: antialiased;
}
.btn-sep {
  padding: 10px 10px 10px 70px;
}
.btn-sep:before {
  background: rgba(0,0,0,0.15);
}
.btn-1 {
  background: #505050;
  color: #fff;
}
.btn-1:hover {
  background: #888888;
}
.btn-1:active {
  background: #818181;
  top: 2px;
}
.btn-1:before {
  position: absolute;
  height: 100%;
  left: 0;
  top: 0px;
  line-height: 2;
  font-size: 140%;
  width: 60px;
}
.icon-plus:before {
  content: "\F055";
}
